import { Rectangle, Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../../core/BaseGame";
import { BaseBonus } from "./BaseBonus";
import gsap, { Expo, Linear } from "gsap";

export class CrazyShipBonus extends BaseBonus {
    private sheep: Sprite;
    private tween: gsap.core.Tween;
    private step: number;
    private currentPoint: [number, number];
    private callback: () => void;
    private getPoint: () => [number, number];
    private sendBounds: boolean;
    constructor(getPoint: () => [number, number]) {
        super();
        this.getPoint = getPoint;
        this.step = 0;
        this.sendBounds = false;
        this.sheep = Sprite.from(Texture.from("sheep.png"));
        this.sheep.anchor.set(0.5, 0.5);
        this.sheep.scale.set(1, 1);
        this.sheep.position.set(0, -800);
        this.addChild(this.sheep);
    }
    public get activeBound(): Rectangle {
        if (this.sendBounds) {
            this.sendBounds = false;
            const point = this.currentPoint;
            this.bound.x = point[0] - 1;
            this.bound.y = point[1] - 1;
            this.bound.width = 2;
            this.bound.height = 2;
            this.step++;
            this.playDelay();
            return this.bound;
        }
        return undefined;
    }
    public start(callback: () => void): void {
        this.callback = callback;
        this.playJump();
    }
    public clean(): void {
        if (this.tween) {
            this.tween.kill();
            this.tween = undefined;
            gsap.killTweensOf(this.sheep.scale);
        }
        this.callback = undefined;
        this.getPoint = undefined;
    }
    private playDelay() {
        gsap.delayedCall(0.3, ()=> this.playJump());
    }
    private playJump() {
        if (this.step < 5 && this.getPoint) {
            this.currentPoint = this.getPoint();
            if (!this.currentPoint) {
                this.callback();
                return;
            }
            const ww = this.currentPoint[0] * METER_SIZE - this.sheep.position.x;
            const hh = this.currentPoint[1] * METER_SIZE - this.sheep.position.y;
            const len = Math.sqrt(ww*ww + hh*hh)/500;
            gsap.to(this.sheep.scale, {x: 1.5, y: 1.5, duration: len/2, ease: Linear.easeNone});
            gsap.to(this.sheep.scale, {x: 0.6, y: 0.6, delay: len/2, duration: len/2, ease: Linear.easeNone});
            this.tween = gsap.to(this.sheep, {x: this.currentPoint[0] * METER_SIZE, y: this.currentPoint[1] * METER_SIZE, duration: len, ease: Expo.easeIn, onComplete: ()=> {
                this.sendBounds = true;
            }});
        } else {
            this.callback();
        }
        
    }
    
}