import gsap from "gsap";
import { AnimatedSprite, Container, Sprite, Text, Texture } from "pixi.js";
export enum EBonus {
    None = 0,
    CrazySheep,
    Alien,
    RainbowBall,
    Magic,
    All
}
export class BonusChooser extends Container {
    public bonus: EBonus;
    private bell: AnimatedSprite;
    private txt: Text;
    constructor() {
        super();
        this.bell = new AnimatedSprite([Texture.from("bell0.png"), Texture.from("bell1.png")], true);
        this.bell.animationSpeed = 0.1;
        this.bell.anchor.set(0.5, 0.5);
        this.addChild(this.bell);
        this.bell.loop = true;

        this.txt = new Text("",{fontSize: 25, fill: 0x000000, align: "right"});
        this.txt.anchor.set(1, 0.5);
        this.txt.position.set(-55, 7);
        this.addChild(this.txt);
    }
    public async show(bonus: EBonus) {
        this.bonus = bonus;
        this.visible = true;
        let n = "";
        switch(bonus) {
            case EBonus.CrazySheep:
                n = "Crazy Sheep";
                break;
            case EBonus.Alien:
                n = "Unexpected Guest";
                break;
            case EBonus.RainbowBall:
                n = "Rainbow Ball";
                break;
            case EBonus.Magic:
                n = "Some Magic is here!";
                break;
        }
        this.bell.play();
        this.txt.text = n;
        this.delay(2).then(()=> {
            this.txt.text = "";
            this.bell.gotoAndStop(0);
        });
        await this.delay(2);
        //this.hide();
    }
    public hide() {
        this.visible = false;
    }
    private async delay (t: number) {
        return new Promise<void>((resolve)=> {
            gsap.delayedCall(t, ()=> {
                resolve();
            })
        });
    }
}