import gsap, { Linear } from "gsap";
import { Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../../core/BaseGame";
import { BaseBonus } from "./BaseBonus";

export class RainbowBallBonus extends BaseBonus {
    private ball: Sprite;
    private tween: gsap.core.Tween;
    constructor(posY: number) {
        super();
        this.ball = Sprite.from(Texture.from("250.png"));
        this.ball.anchor.set(0.5, 0.5);
        this.ball.scale.set(1, 1);
        this.ball.position.set(-500, posY);
        this.addChild(this.ball);
    }
    public start(callback: ()=>void): void {
        this.tween = gsap.to(this.ball, {x: 500, duration: 1, ease: Linear.easeNone, onUpdate: ()=> {
            this.ball.angle += 5;
        }, onComplete: ()=> {
            callback();
        }});
    }
    public clean(): void {
        if (this.tween) {
            this.tween.kill();
            this.tween = undefined;
        }
    }
    public get activeBound() {
        if (this.tween) {
            this.bound.x = (this.ball.x - this.ball.width * 0.5) / METER_SIZE;
            this.bound.y = (this.ball.y - this.ball.height * 0.5) / METER_SIZE;
            this.bound.width = this.ball.width / METER_SIZE;
            this.bound.height = this.ball.height / METER_SIZE;
            return this.bound;
        } else {
            return undefined;
        }
    }
}