import { Rectangle, Sprite, Texture } from "pixi.js";
import { METER_SIZE } from "../../../../core/BaseGame";
import { BaseBonus } from "./BaseBonus";
import gsap, { Expo, Linear } from "gsap";

export class AlienBonus extends BaseBonus {
    private alien: Sprite;
    private acid: Sprite;
    private tween: gsap.core.Tween;
    private step: number;
    private currentPoint: [number, number];
    private callback: () => void;
    private getPoint: () => [number, number];
    private sendBounds: boolean;
    constructor(getPoint: () => [number, number]) {
        super();
        this.getPoint = getPoint;
        this.step = 0;
        this.sendBounds = false;

        this.acid = Sprite.from(Texture.from("alien_acid.png"));
        this.acid.anchor.set(0.5, 0.5);
        this.acid.scale.set(0.7, 0.7);
        this.acid.position.set(800, 50);
        this.addChild(this.acid);
        this.acid.visible = false;

        this.alien = Sprite.from(Texture.from("alien.png"));
        this.alien.anchor.set(1, 0.5);
        this.alien.position.set(800, 0);
        this.addChild(this.alien);
    }
    public get activeBound(): Rectangle {
        if (this.sendBounds) {
            this.sendBounds = false;
            const point = this.currentPoint;
            this.bound.x = point[0] - 1;
            this.bound.y = point[1] - 1;
            this.bound.width = 2;
            this.bound.height = 2;
            this.step++;
            this.playDelay();
            return this.bound;
        }
        return undefined;
    }
    public start(callback: () => void): void {
        this.callback = callback;
        gsap.to(this.alien, {x: 350, duration: 0.5, ease: Linear.easeNone, onComplete: ()=> {
            this.playJump();
        }});
        
    }
    public clean(): void {
        if (this.tween) {
            this.tween.kill();
            this.tween = undefined;
            gsap.killTweensOf(this.acid.scale);
        }
        this.callback = undefined;
        this.getPoint = undefined;
    }
    private playDelay() {
        gsap.delayedCall(0.1, ()=> this.playJump());
    }
    private playJump() {
        if (this.step < 5 && this.getPoint) {
            this.currentPoint = this.getPoint();
            if (!this.currentPoint) {
                this.callback();
                return;
            }
            this.acid.position.set(300, -40);
            this.acid.visible = true;
            this.tween = gsap.to(this.acid, {x: this.currentPoint[0] * METER_SIZE, y: this.currentPoint[1] * METER_SIZE, duration: 0.7, ease: Expo.easeIn, onComplete: ()=> {
                this.sendBounds = true;
                this.acid.visible = false;
                const splash = Sprite.from(Texture.from("splash.png"));
                splash.anchor.set(0.5, 0.5);
                splash.position.copyFrom(this.acid.position);
                this.addChild(splash);
                gsap.to(splash, {alpha: 0, duration: 1, ease: Linear.easeNone});
                gsap.to(splash.scale, {y: 1.5, duration: 1, ease: Linear.easeNone, onComplete: ()=> {
                    this.removeChild(splash);
                }});
            }});
        } else {
            this.callback();
        }
        
    }
    
}